@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.thought-bubble {
  position: absolute;
  bottom: 130%; /* Position above the heart */
  left: 50%;
  transform: translateX(-50%) translateY(50%);
  background-color: white;
  border: 2px solid black;
  border-radius: 25px; /* This makes the bubble round */
  padding: 15px;
  width: max-content;
  max-width: 80%; /* Adjust as needed */
  text-align: center;
  font-size: 1rem;
  z-index: 10;
  /* Tailwind classes equivalent for font and other styles can be used */
}

.thought-bubble::before,
.thought-bubble::after {
  content: "";
  position: absolute;
  bottom: -20px; /* Adjust as needed */
  left: 50%;
  background-color: white;
  border: 4px solid black;
  border-radius: 50%; /* Makes the dots circular */
  z-index: 9;
}

.thought-bubble::before {
  width: 20px; /* Size of the first thought dot */
  height: 20px;
  transform: translate(-50%, 0);
}

.thought-bubble::after {
  width: 30px; /* Size of the second thought dot */
  height: 30px;
  transform: translate(-50%, 20px); /* Adjust as needed */
}
